import React, { createContext, useState } from 'react';

export const AppContext = createContext();

export const AppProvider = ({ children }) => {
  const [isNavbarCategoryOpen, setIsNavbarCategoryOpen] = useState(false);
  const [isOpenFlashSale, setIsOpenFlashSale] = useState(true);

  return (
    <AppContext.Provider value={{ isNavbarCategoryOpen, setIsNavbarCategoryOpen, setIsOpenFlashSale, isOpenFlashSale }}>
      {children}
    </AppContext.Provider>
  )
}
